//
// Color
//

// basic
$color-primary-dark                   : darken(rgb(255, 181, 062), 25%); // #ffb53e
$color-primary                        : rgb(255, 181, 062); // #ffb53e
$color-primary-light                  : lighten(rgb(255, 181, 062), 25%); // #ffb53e

// gray
$gray-black                           : rgb(000, 000, 000); // #000000
$gray-darkest                         : rgb(034, 034, 034); // #222222
$gray-darker                          : rgb(065, 065, 065); // #414141
$gray-dark                            : rgb(096, 096, 096); // #606060
$gray                                 : rgb(186, 186, 186); // #bababa
$gray-light                           : rgb(204, 204, 204); // #cccccc
$gray-lighter                         : rgb(226, 226, 226); // #e2e2e2
$gray-lightest                        : rgb(252, 252, 252); // #fcfcfc
$gray-white                           : rgb(255, 255, 255); // #ffffff


//
// Transition
//

$transition-mode                      : cubic-bezier( .25, .8, .25, 1);


//
// Texting
//

$font-family                          : "Lato", "system", "-apple-system", "PingFang-TC", "Segoe UI", "Microsoft JhengHei", "Roboto", "Oxygen", "Fira Sans", "Helvetica Neue", "Helvetica", "Arial", "sans-serif";

$font-size-xl                         : 20px;
$font-size-lg                         : 18px;
$font-size-md                         : 16px;
$font-size-sm                         : 14px;
$font-size-xs                         : 12px;

@mixin full-fill($position: absolute) {
  position: $position;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

@mixin image-fluid {
  max-width: 100%;
  height: auto;
}

@mixin image-centre($height, $size: "contain", $child: "img") {
  height: $height;
  line-height: $height;
  text-align: center;
  overflow: hidden;

  > #{$child} {
    @include image-centre-child($size);
  }
}

@mixin image-centre-child($size: "contain") {
  margin: -100%;

  @if $size == "contain" {
    max-height: 100%;
    max-width: 100%;
  } @else if $size == "cover-by-width" {
    min-width: 100%;
  } @else if $size == "cover-by-height" {
    min-height: 100%;
  }
}

@mixin position-centre($position: absolute) {
  position: $position;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@mixin position-center-over-top($transform...) {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -100%) #{$transform};
}

@mixin position-center-over-right($transform...) {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(100%, -50%) #{$transform};
}

@mixin position-center-over-bottom($transform...) {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 100%) #{$transform};
}

@mixin position-center-over-left($transform...) {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-100%, -50%) #{$transform};
}

@mixin position-top-over-left($transform...) {
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-100%, 0%) #{$transform};
}

@mixin position-top-over-right($transform...) {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(100%, 0%) #{$transform};
}

@mixin position-right-over-top($transform...) {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(0%, -100%) #{$transform};
}

@mixin position-right-over-bottom($transform...) {
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translate(0%, 100%) #{$transform};
}

@mixin position-left-over-top($transform...) {
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(0%, -100%) #{$transform};
}

@mixin position-left-over-bottom($transform...) {
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translate(0%, 100%) #{$transform};
}

@mixin position-bottom-over-left($transform...) {
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translate(-100%, 0%) #{$transform};
}

@mixin position-bottom-over-right($transform...) {
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translate(100%, 0%) #{$transform};
}

@mixin size($width, $height: $width) {
  width: $width;
  height: $height;
}

/**
 * Pure CSS text truncate
 * See [Line Clampin' (Truncating Multiple Line Text) - Weird WebKit Flexbox Way](https://css-tricks.com/line-clampin/)
 */

@mixin text-truncate-singleline($width: 100%) {
  // Requires inline-block or block for proper styling

  width: $width;
  white-space: nowrap;
  @include text-truncate-style;
}

@mixin text-truncate-multiline($line-to-show, $line-height: 1.5, $font-size: 1em) {
  display: block; // fallback
  display: -webkit-box;
  line-height: $line-height;
  max-height: $font-size * $line-height * $line-to-show; // fallback
  -webkit-line-clamp: $line-to-show;
  @include text-truncate-style;

  // *** IMPORTANT SEE ***:
  // 1. https://github.com/postcss/autoprefixer/issues/776#issuecomment-392346351
  // 2. https://github.com/NoName4Me/yo-FE/issues/3
  // eslint-disable-next-line
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
}

@mixin text-truncate-style {
  overflow: hidden;
  text-overflow: ellipsis;
}

$letter-spacing: 0.1em;
$font-weight: 900;

$border-width: 2px;
$border-style: solid;

// ---
// types
$types: (
  default: (
    filled-true-color: $gray-light,
    filled-true-color-activate: $gray-lighter,
    filled-true-border-color: transparent,
    filled-true-border-color-activate: transparent,
    filled-true-background-color: $gray-white,
    filled-true-background-color-activate: $gray-white,
    filled-false-color: $color-primary,
    filled-false-color-activate: $color-primary-dark,
    filled-false-border-color: currentColor
  ),
  primary: (
    filled-true-color: $gray-white,
    filled-true-color-activate: $gray-white,
    filled-true-border-color: transparent,
    filled-true-border-color-activate: transparent,
    filled-true-background-color: $gray-darkest,
    filled-true-background-color-activate: $color-primary,
    filled-false-color: $gray-darkest,
    filled-false-color-activate: $color-primary,
    filled-false-border-color: currentColor
  ),
  gray: (
    filled-true-color: $gray-white,
    filled-true-color-activate: $gray-white,
    filled-true-border-color: transparent,
    filled-true-border-color-activate: transparent,
    filled-true-background-color: $gray-darkest,
    filled-true-background-color-activate: $gray-darker,
    filled-false-color: $gray-darkest,
    filled-false-color-activate: $gray-darker,
    filled-false-border-color: currentColor
  )
);

// sizes
$sizes: (
  sm: (
    padding-horizontal: 15px,
    height: 40px,
    font-size: $font-size-xs
  ),
  md: (
    padding-horizontal: 20px,
    height: 50px,
    font-size: $font-size-sm
  )
);

// shapes
$shapes: (
  radius: (
    sm: 3px,
    md: 5px
  ),
  rounded: (
    sm: 100%,
    md: 100%
  ),
  circle: (
    sm: 50%,
    md: 50%
  )
);

// disabled
$disabled-filled-true-color: $gray;
$disabled-filled-true-border-color: transparent;
$disabled-filled-true-background-color: $gray-light;
$disabled-filled-false-color: $gray;
$disabled-filled-false-border-color: currentColor;

.button {
  cursor: pointer;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  letter-spacing: $letter-spacing;
  font-weight: $font-weight;
  border-width: $border-width;
  border-style: $border-style;
  vertical-align: middle;
  transition: all 200ms $transition-mode;

  @each $type, $styles in $types {
    &:not(:disabled)[data-type="#{$type}"] {
      &[data-is-filled="true"] {
        color: map-get($styles, filled-true-color);
        border-color: map-get($styles, filled-true-border-color);
        background-color: map-get($styles, filled-true-background-color);

        &:not(:disabled):hover,
        &:not(:disabled):focus {
          color: map-get($styles, filled-true-color-activate);
          border-color: map-get($styles, filled-true-border-color-activate);
          background-color: map-get($styles, filled-true-background-color-activate);
        }
      }

      &[data-is-filled="false"] {
        color: map-get($styles, filled-false-color);
        border-color: map-get($styles, filled-false-border-color);
        background-color: transparent;

        &:not(:disabled):hover,
        &:not(:disabled):focus {
          color: map-get($styles, filled-false-color-activate);
        }
      }
    }
  }

  &[data-is-block="true"] {
    display: flex;
    width: 100%;
  }

  &[data-is-block="false"] {
    display: inline-flex;
  }

  @each $size, $styles in $sizes {
    &[data-size="#{$size}"] {
      height: map-get($styles, height);
      padding: 0 map-get($styles, padding-horizontal);
      font-size: map-get($styles, font-size);
    }
  }

  @each $shape, $shape-sizes in $shapes {
    &[data-shape="#{$shape}"] {
      @each $shape-size, $border-radius in $shape-sizes {
        &[data-size="#{$shape-size}"] {
          @if ($shape == "circle") {
            padding: 0;
            width: map-get(map-get($sizes, $shape-size), height);
            border-radius: $border-radius;
          } @else if ($shape == "rounded") {
            border-radius: #{map-get(map-get($sizes, $shape-size), height)} / $border-radius;
          } @else {
            border-radius: $border-radius;
          }
        }
      }
    }
  }

  &:disabled {
    &[data-is-filled="true"] {
      color: $disabled-filled-true-color;
      border-color: $disabled-filled-true-border-color;
      background-color: $disabled-filled-true-background-color;
    }

    &[data-is-filled="false"] {
      color: $disabled-filled-false-color;
      border-color: $disabled-filled-false-border-color;
      background-color: transparent;
    }
  }
}
